.main {
  margin-top: 60px;
  padding: 20px 40px;
  transition: all 0.3s;
  @media (min-width: 990px) {
    margin-left: 250px;
  }
  @media (max-width: 989px) {
    padding: 20px;
  }

  &.menu_close {
    margin-left: 0px !important;
  }
}

.menu {
  display: flex;
  flex-direction: column;
  position: fixed;
  width: 250px;
  top: 0;
  bottom: 0;
  left: 0;
  background-color: #3c4b64;
  transition: all 0.3s;
  &.menu_close {
    left: -250px;
  }
}
